export const types = {
  NOT_EMPTY5: 'NOT_EMPTY5',
  NOT_LONGER30: 'NOT_LONGER30',
  NOT_EMPTY10: 'NOT_EMPTY10',
  RADIO_NOT_SELECTED: 'RADIO_NOT_SELECTED',
  FILE_NOT_PROVIDED: 'FILE_NOT_PROVIDED',
  FILE_DATA: {
    GENERAL: 'FILE_DATA_GENERAL',
    INSUFFICIENT_COL_NUMBER: 'INSUFFICIENT_COL_NUMBER',
    NO_EMPTY_CELLS: 'NO_EMPTY_CELLS',
    WRONG_HEADER: 'WRONG_HEADER',
    AT_LEAST_ONE_PRODUCT: 'WRONG_HEADER',
    // DUTY_IS_NOT_NUMBER: 'DUTY_IS_NOT_NUMBER',
    // PRICE_IS_NOT_NUMBER: 'PRICE_IS_NOT_NUMBER',
    // QTY_IS_NOT_NUMBER: 'QTY_IS_NOT_NUMBER',
    // G_WEIGHT_IS_NOT_NUMBER: 'G_WEIGHT_IS_NOT_NUMBER',
    // CBM_IS_NOT_NUMBER: 'CBM_IS_NOT_NUMBER',
    // CURRENCY_IS_NOT_STRING: 'CURRENCY_IS_NOT_STRING',
    // UM_IS_NOT_STRING: 'UM_IS_NOT_STRING',
  },
};

export const messages = {
  [types.NOT_EMPTY5]: 'Error: field must have at least 5 symbols',
  [types.NOT_LONGER30]: 'Error: field must have at most 30 symbols',
  [types.NOT_EMPTY10]: 'Error: field must have at least 10 symbols',
  [types.RADIO_NOT_SELECTED]: 'Error: please select an option',
  [types.FILE_NOT_PROVIDED]: 'Error: file must be provided',
  [types.FILE_DATA.GENERAL]: 'Error: wrong file format',
};

export const validators = {
  validateNotEmpty5: field => (field.length < 5 ? messages.NOT_EMPTY5 : null),
  validateNotLonger30: field => (field.length >= 30 ? messages.NOT_LONGER30 : null),
  validateNotEmpty10: field => (field.length < 10 ? messages.NOT_EMPTY10 : null),
  validateRadioNotSelected: field => (field === '' ? messages.RADIO_NOT_SELECTED : null),
  validateFileNotProvided: field => (field === '' ? messages.FILE_NOT_PROVIDED : null),
  validateDataFormat: ({ data, cols }) => {
    const headers = [
      'Item code',
      'Item Description',
      'U.M.',
      'Quantity, U.M.',
      'Price per U.M.',
      'Currency',
      'Gross Weight, kg',
      'Volume, CBM',
      'Duty tariff, %',
    ];
    const generalFileDataErrorMsg = messages[types.FILE_DATA.GENERAL];
    if (cols.length !== 9) {
      return `${generalFileDataErrorMsg} (${types.FILE_DATA.INSUFFICIENT_COL_NUMBER})`;
    }
    if (data.length < 2) {
      return `$generalFileDataErrorMsg} (${types.FILE_DATA.AT_LEAST_ONE_PRODUCT})`;
    }
    if ([...data].some(cell => cell.length === 0)) {
      return `${generalFileDataErrorMsg} (${types.FILE_DATA.NO_EMPTY_CELLS})`;
    }
    if (data[0].some((header, idx) => header !== headers[idx])) {
      return `${generalFileDataErrorMsg} (${types.FILE_DATA.WRONG_HEADER})`;
    }
    return null;
  },
};

export default types;
